import React, { Component } from 'react';
import { connect } from 'react-redux';
import { ConfigProvider, Root, View } from '@vkontakte/vkui';

import { RouteNode } from 'react-router5'
import '@vkontakte/vkui/dist/vkui.css';
import '../css/style.css';
import * as vkSelectors from '../store/vk/reducer';
import * as vkActions from '../store/vk/actions';


import Main from './Main';
import Choice from './Choice';
import MainInput from './MainInput';
import Consent from './Consent';
import Profile from './Profile';
import Todo from './Todo';
import Events from './Events';
import Search from './Search';

class App extends Component {

    componentWillMount() {
        this.props.dispatch(vkActions.initApp());
    }

    render() {
        let activePanel = this.props.route.name;
        
        return (
            <ConfigProvider insets={this.props.insets}>
                <Root activeView="mainView">
                    <View id="mainView" activePanel={activePanel}>
                        <Main router={this.props.router} id="main" />
                        <Choice router={this.props.router} id="choice"  />
                        <MainInput router={this.props.router} id="input"  />
                        <Consent router={this.props.router} id="consent"  />
                        <Profile router={this.props.router} id="profile"  />
                        <Todo router={this.props.router} id="todo"  />
                        <Events router={this.props.router} id="events"  />
                        <Search router={this.props.router} id="search"  />
                    </View>
                </Root>
            </ConfigProvider>
        );
    }
}

function mapStateToProps(state) {
    return {
        accessToken: vkSelectors.getAccessToken(state),
        insets: vkSelectors.getInsets(state),
    };
}

export default connect(mapStateToProps)(
    (props) => (
        <RouteNode nodeName="">
            {({ route }) => <App route={route} {...props}/>}
        </RouteNode>
    )
);
