import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, Button } from '@vkontakte/vkui';
import * as vkSelectors from '../store/vk/reducer';

import MainHeader from './MainHeader';

class Consent extends Component {

    openNext = () => {
        this.props.router.navigate('profile');
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Div style={{
                    height: 'calc(100vh - 85px)',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                }}>
                    <Div style={{textAlign: 'center', marginBottom: 150}}>
                        <h1>Хотите добавить еще питомца?</h1>
                    </Div>
                    <Div>
                        <Div style={{ margin: '0 auto', width: '250px', display: 'flex', justifyContent: 'space-between' }}>
                            <Button><span style={{fontSize: 24, display: 'inline-block', padding: 10}}>Да</span></Button>
                            <Button onClick={this.openNext}><span style={{fontSize: 24, display: 'inline-block', padding: 10}}>Нет</span></Button>
                        </Div>
                    </Div>
                </Div>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(Consent);
