import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Panel } from '@vkontakte/vkui';
import * as vkSelectors from '../store/vk/reducer';

import MainHeader from './MainHeader';
import Bar from './Bar';

class Todo extends Component {



    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Fragment>
                    <h1 className="title_custom_main">Список дел</h1>
                    <div className="todo-wrapper">
                        <div className="todo-input-wrapper">
                            <i className="fas fa-plus" />
                            <input
                            className="todo-input"
                            placeholder="Click to add task"
                            />
                        </div>
                        <ul className="todo-list">
                            {[{id: 1, text: 'txt'}].map(({ id, text}) => (
                            <li className="todo-item" key={id}>
                                <i className='mark far fa-check-circle' />
                                <span className='completed text'>{text}</span>
                                <i className="fas fa-times" />
                            </li>
                            ))}
                        </ul>
                        <div className="footer">
                            <span className="amount">Tasks left</span>
                            <div className="btn-group">
                                <button className="filter-btn active">text</button>
                            </div>
                        </div>
                    </div>
                </Fragment>
                <Bar />
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(Todo);
