import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Input, Div, Group, List, Cell } from '@vkontakte/vkui';

import { debounce } from '../store/hack/actions'

import MainHeader from './MainHeader';

class Search extends Component {

    constructor(props) {
        super(props);

        this.state = {
            search: '',
            list: {}
        }
    }

    maps = () => {
        let api = `https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${this.state.search}&apikey=a57932b2-075e-4907-994d-1e7cd5b048d2`;
        fetch(api)
        .then(res => res.json())
        .then(data => this.setState({
            list: data.response.GeoObjectCollection.featureMember
        }))
        .catch(e => console.log(e))
    }

    request = debounce(() => this.maps());

    ChangeSearch = (e) => { 
        this.setState({ search: e.target.value }); 
        this.request();
      }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={() => {}} />
                <Div>
                    <Input type="text" placeholder="Поиск (Вит оптек)" onChange={this.ChangeSearch} value={this.state.search} />
                </Div>
                <Group>
                    {/* <List>
                        {this.state.list.map((list, index) => <Cell 
                            key={index} 
                            multiline={true} 
                            description={`${list.GeoObject.metaDataProperty.GeocoderMetaData.text}`}
                            >
                           
                        </Cell>)
                        }
                    </List> */}
                </Group>
            </Panel>
        );
    }

}

function mapStateToProps(state) {
    return {};
}

export default connect(mapStateToProps)(Search);
