import Immutable from 'seamless-immutable';
import _ from 'lodash';
import * as types from './actionTypes';

const initialState = Immutable({
  mainAppState: undefined,
  pet: {
      name: '',
      date: ''
  }
});

export default function reduce(state = initialState, action = {}) {
    switch (action.type) {
        case types.INFO_PET_NAME:
            return {
                ...state,
                pet: {
                    ...state.pet,
                    name: action.name
                }
            };
        case types.INFO_PET_DATE:
            return {
                ...state,
                pet: {
                    ...state.pet,
                    date: action.date
                }
            };
        default:
            return state;
    }
}

export function getName(state) {
    return state.appState.pet.name;
}

export function getDate(state) {
    return state.appState.pet.date;
}
