import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Avatar, Group, Div } from '@vkontakte/vkui';

import Icon28WriteOutline from '@vkontakte/icons/dist/28/write_outline';

import Icon56GalleryOutline from '@vkontakte/icons/dist/56/gallery_outline';
import Icon56EventOutline from '@vkontakte/icons/dist/56/event_outline';
import Icon56WriteOutline from '@vkontakte/icons/dist/56/write_outline';
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';

import '../css/RichCell.css';
import * as vkSelectors from '../store/vk/reducer';
import * as getActions from '../store/hack/reducer';

import avaImg from '../image/ava.png';

import MainHeader from './MainHeader';
import Bar from './Bar';
import Preview from '../image/pre.png'

class Profile extends Component {

    openPageEvents = () => {
        this.props.router.navigate('events');
    }

    openPageTodo = () => {
        this.props.router.navigate('todo');
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        const { name, date } = this.props;
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Group>
                    <div className="Tappable_custom">
                        <Avatar size={72} src={avaImg} />
                        <div className="RichCell__in">
                            <div className="RichCell__top">
                        
                                <div className="RichCell__content">
                                    <div className="RichCell__children RichCell__title">{name || 'Нет имени'}</div>
                                    <div className="RichCell__after" style={{position: 'relative'}}>
                                        <Icon28WriteOutline style={{position: 'absolute', right: 0, top: 0, cursor: 'pointer', color: '#3F8AE0'}} /></div>
                                </div>
                                <div className="RichCell__text">{date || 'Нет даты'}</div>
                                <div className="RichCell__caption">0 г (л). 0 мес. 00 дн.</div>
                            
                            </div>
                        </div>
                    </div>

                    <Div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between', margin: '30px 0 0' }}>
                        <Div style={{height: 72, width: 72, borderRadius: 6, padding: 0, color: '#3F8AE0'}}>
                            <Icon56GalleryOutline width={72} height={72} />
                        </Div>
                        <Div onClick={this.openPageEvents} style={{height: 72, width: 72, borderRadius: 6, padding: 0, color: '#3F8AE0'}}>
                            <Icon56EventOutline width={72} height={72} />
                        </Div>
                        <Div onClick={this.openPageTodo} style={{height: 72, width: 72, borderRadius: 6, padding: 0, color: '#3F8AE0'}}>
                            <Icon56WriteOutline width={72} height={72} />
                        </Div>
                        <Div style={{height: 72, width: 72, borderRadius: 6, padding: 0, color: '#3F8AE0'}}>
                            <Icon56ErrorOutline width={72} height={72} />
                        </Div>
                    </Div>

                    <Div>
                        <h1 style={{margin: 0}}>Фотографии</h1>
                    </Div>
                    
                    <Div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between' }}>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                    </Div>
                    <Div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between' }}>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                    </Div>
                    <Div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between' }}>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                    </Div>
                    <Div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between' }}>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                        <Div className="bgPreview" style={{height: 72, width: 72, borderRadius: 6, padding: 0}}></Div>
                    </Div>
                </Group>

                <Bar />
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
        name: getActions.getName(state),
        date: getActions.getDate(state)
    };
}

export default connect(mapStateToProps)(Profile);
