import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, Input, Button, Select } from '@vkontakte/vkui';
import * as vkSelectors from '../store/vk/reducer';

import * as Actions from '../store/hack/actions';
import { bindActionCreators } from 'redux';

import MainHeader from './MainHeader';

class MainInput extends Component {

    constructor (props, context) {
        super(props, context);
        this.state = {
          name: '',
          date: ''
        };
      }

    openNext = () => {
        const { dispatch } = this.props;
        const actions = bindActionCreators(Actions, dispatch);
        actions.setName(this.state.name);
        actions.setDate(this.state.date);
        this.props.router.navigate('consent');
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    handleChangeName = (e) => {
        this.setState({ name: e.target.value });
    }

    handleChangeDate = (e) => {
        this.setState({ date: e.target.value });
    }

    render() {
        
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Div style={{
                    height: 'calc(100vh - 85px)',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                }}>
                    <Div style={{
                        height: '90%',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center'
                    }}>
                        <Div style={{ textAlign: 'center', width: '100%' }}>
                            <h1>Питомец</h1>
                            <Input type="text" placeholder="Введите кличку" value={this.state.name} onChange={this.handleChangeName} />
                        </Div>
                        <Div style={{ width: '100%' }}>
                            <Input type="date" onChange={this.handleChangeDate} />
                        </Div>
                        <Div style={{ width: '100%' }}>
                            <Select placeholder="Самец/самка">
                                <option value="m">Самец</option>
                                <option value="f">Самка</option>
                            </Select>
                        </Div>
                        <Div style={{ width: '100%' }}>
                            <Select placeholder="Не стерилизован(а)/стерилизован(а)">
                                <option value="m">Не стерилизован(а)</option>
                                <option value="f">Стерилизован(а)</option>
                            </Select>
                        </Div>
                    </Div>
                    <Div>
                        <Button size="xl" component="a" onClick={this.openNext}>Продолжить</Button>
                    </Div>
                </Div>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(MainInput);
