import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, Button } from '@vkontakte/vkui';
import * as vkSelectors from '../store/vk/reducer';

import MainHeader from './MainHeader';
import MainCalendar from './MainCalendar';
import Bar from './Bar';

class Events extends Component {

    componentWillMount() {
        //this.props.dispatch(currencyRatesActions.fetchUsdEurRate());
    }

    openNext = () => {
        //this.props.router.navigate('input');
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <MainCalendar />
                <Bar />
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(Events);
